.row, .container, .inp-form, .inp-wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

* {
  box-sizing: border-box; }

html {
  font-size: 10px; }

body {
  font: 16px "Cabin", sans-serif; }

.wrap {
  width: 940px;
  margin: 20px auto; }

.row {
  margin-top: 30px; }

.btn,
.btn-round,
.btn-transparent,
.btn-sandwitch,
.btn-toggle {
  margin: 0;
  border: none;
  color: #fff;
  background-color: #65ABD0; }

.btn--lg {
  width: 190px;
  height: 60px;
  font-size: 18px; }

.btn--md {
  width: 140px;
  height: 48px; }

.btn--sm {
  width: 120px;
  height: 36px;
  font-size: 14px; }

.btn-round, .btn-transparent {
  border-radius: 2em; }

.btn-transparent {
  border: 2px solid #65ABD0;
  color: #65ABD0;
  background-color: transparent; }

.btn-next,
.btn-prev {
  position: relative;
  border: none;
  border-radius: 50%;
  background-color: #65ABD0; }
  .btn-next:after,
  .btn-prev:after {
    font-family: "social";
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff; }

.btn-prev:after {
  transform: translate(-50%, -50%) rotate(180deg); }

.btn-prev--square {
  border-radius: 5px 0 0 5px; }

.btn-next--square {
  border-radius: 0 5px 5px 0;
  border-left: 1px solid rgba(0, 0, 0, 0.2); }

.btn-next--lg {
  width: 60px;
  height: 60px; }

.btn-next--md,
.btn-sandwitch--md {
  width: 48px;
  height: 48px; }
  .btn-next--md:after,
  .btn-sandwitch--md:after {
    font-size: 15px; }

.btn-next--sm,
.btn-sandwitch--sm {
  width: 36px;
  height: 36px; }
  .btn-next--sm:after,
  .btn-sandwitch--sm:after {
    font-size: 11px; }

.btn-sandwitch {
  border-radius: 10px; }
  .btn-sandwitch span {
    display: block;
    width: 50%;
    height: 2px;
    margin: 10% auto;
    background-color: #fff; }

.btn-pointer, .btn-pointer--sm {
  position: relative;
  border: none;
  border-radius: 5px 0 0 5px;
  background-color: #65ABD0;
  color: #fff; }
  .btn-pointer:after, .btn-pointer--sm:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(100%);
    border-style: solid;
    border-color: transparent transparent transparent #65ABD0; }

.btn-pointer {
  width: 75px;
  height: 48px; }
  .btn-pointer:after {
    border-width: 24px 0 24px 24px; }

.btn-pointer--sm {
  width: 94px;
  height: 36px;
  font-size: 14px; }
  .btn-pointer--sm:after {
    border-width: 18px 0 18px 18px; }

.btn-toggle {
  position: relative;
  background-color: #24AA98; }
  .btn-toggle .sw-wrap {
    width: 19px;
    height: 19px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-flow: row wrap;
    align-items: center; }
  .btn-toggle .sw-topper, .btn-toggle .sw-middle, .btn-toggle .sw-bottom {
    width: 100%;
    height: 2px;
    background-color: #fff; }
  .btn-toggle .sw-topper {
    width: 80%; }
  .btn-toggle .sw-bottom {
    width: 50%; }

.btn-toggle--cent .sw-wrap {
  justify-content: center; }

.btn-toggle--end .sw-wrap {
  justify-content: flex-end; }

.inp-form {
  position: relative; }
  .inp-form input {
    width: 219px;
    height: 48px;
    padding-left: 20px;
    border: 2px solid #c6ccd2;
    border-right: none;
    border-radius: 5px 0 0 5px; }
  .inp-form__btn {
    background-color: #24AA98;
    width: 76px;
    height: 48px;
    border: none;
    border-radius: 0 5px 5px 0;
    color: #fff; }
  .inp-form__select {
    width: 112px;
    height: 48px;
    padding-left: 18px;
    background-color: #24AA98;
    border-radius: 5px 0 0 5px;
    border: none;
    color: #fff; }
    .inp-form__select--ok {
      width: 60px;
      padding-left: 10px;
      border-radius: 6px; }
  .inp-form--2-btns input {
    border-radius: 0;
    border-left: none; }

.inp-wrap {
  position: relative; }
  .inp-wrap input {
    width: 220px;
    height: 48px;
    border: 2px solid #c6ccd2; }
  .inp-wrap input:focus + .icon--left {
    color: #000; }
  .inp-wrap input:focus ~ .icon--right {
    opacity: 1; }
  .inp-wrap span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #c6ccd2;
    font-size: 16px; }
  .inp-wrap .icon--left {
    left: 25px; }
  .inp-wrap .icon--right {
    right: 25px;
    opacity: 0; }

.inp--mail {
  padding-left: 20px;
  border-radius: 6px; }

.inp--search {
  padding-left: 40px;
  border-radius: 30px; }

.inp--suc:focus {
  border-color: #24AA98; }

.inp--suc:focus + span {
  right: 20px;
  font-size: 12px;
  color: #24AA98; }

.inp--error:focus {
  border-color: red; }

.inp--error:focus + span {
  right: 20px;
  font-size: 10px;
  color: red; }

.btn-search {
  border: none;
  border-radius: 5px;
  background-color: #24AA98;
  padding: .9em 1.6em; }
  .btn-search:before {
    font-family: "social";
    content: "";
    color: #fff; }

.inp-search {
  width: 160px;
  height: 48px;
  border: 2px solid #c6ccd2;
  border-radius: 5px;
  padding-left: 20px; }

.src-wrap .btn-search {
  position: absolute;
  right: 0;
  border-radius: 0 5px 5px 0;
  background-color: #c6ccd2; }

.src-wrap input:focus + .btn-search {
  background-color: #24AA98; }
